import { Model } from './model';
import * as d3 from 'd3';
import { Chart } from './chart';
import { data } from './data';
export var draw = function (element) {
    var _a = d3.select(element).node().getBoundingClientRect(), width = _a.width, height = _a.height;
    var w = width * 90 / 100;
    var h = height * 90 / 100;
    var svg = d3.select(element)
        .append("svg")
        .attr("width", width)
        .attr("height", height);
    var model = new Model(data);
    var chart = new Chart(model, w, h);
    // svg.selectAll("dot")
    //   .data([
    //     ...chart.getLeftInnerPoints(),
    //     ...chart.getRightInnerPoints(),
    //     chart.getLeftOuterTopPoint(),
    //     chart.getLeftOuterBottomPoint(),
    //     chart.getRightOuterTopPoint(),
    //     chart.getRightOuterBottomPoint(),
    //     chart.getLeftOuterMiddlePoint(),
    //     chart.getRightOuterMiddlePoint()
    //   ])
    //   .enter().append("circle")
    //   .attr("r", 1.5)
    //   .attr("cx", function (d: number[]) {
    //     return d[0];
    //   })
    //   .attr("cy", function (d: number[]) {
    //     return d[1];
    //   });
    // svg.selectAll("dot")
    //   .data(chart.getLeftInnerPointsForLinks())
    //   .enter().append("circle")
    //   .attr("r", 2)
    //   .attr("cx", function (d: number[]) {
    //     return d[0];
    //   })
    //   .attr("cy", function (d: number[]) {
    //     return d[1];
    //   });
    var markerWidth = chart.getMarkerWidth();
    svg.append("svg:defs")
        .selectAll("marker")
        .data(chart.getLines())
        .enter().append("svg:marker")
        .attr("id", function (d) { return "arrow" + d.id; })
        .attr("refX", 0.27).attr("refY", 0)
        .attr("markerWidth", markerWidth)
        .attr("markerHeight", function (d) { return "" + d.width; })
        .attr("orient", "auto")
        .attr("markerUnits", "userSpaceOnUse")
        .attr("viewBox", function (d) { return "0 0 " + markerWidth + " 1"; })
        .append("svg:path")
        .attr("fill", function (d) { return d.color; })
        .attr("stroke", "none")
        .attr("stroke-width", 0)
        .attr("d", function (d) { return "M0,-" + d.width / 2 + "L" + markerWidth + ",0L0," + d.width / 2; });
    svg.selectAll('path.big-line')
        .data(chart.getLines())
        .enter().append("path")
        .attr("id", function (d) { return "big-line" + d.id; })
        .attr("d", function (d) { return "M" + d.line[0] + " Q" + d.line[1] + " " + d.line[2]; })
        .attr("stroke-width", function (d) { return d.width; })
        .attr("fill", "none")
        .attr("stroke", function (d) { return d.color; })
        .attr("class", "big-line")
        .attr('marker-end', function (d) { return d.marker ? "url(#arrow" + d.id + ")" : null; }); //attach the arrow from defs
    svg.selectAll('text.big-line-name')
        .data(chart.getLines())
        .enter().append("text")
        .attr("dy", function (d) { return d.subTitle ? -3 : 3; })
        .attr("dx", 0)
        .attr("class", "big-line-name")
        .append("textPath")
        .attr("xlink:href", function (d) { return "#big-line" + d.id; })
        .style("text-anchor", "middle")
        .attr("stroke", function (d) { return d.nameColor; })
        .attr("startOffset", "50%")
        .attr("font-size", width * 0.018 + "px")
        .text(function (d) { return "" + d.name; });
    svg.selectAll('text.big-line-sub-title')
        .data(chart.getLines().filter(function (line) { return !!line.subTitle; }))
        .enter().append("text")
        .attr("dy", 14)
        .attr("dx", 0)
        .attr("class", "big-line-sub-title")
        .append("textPath")
        .attr("xlink:href", function (d) { return "#big-line" + d.id; })
        .style("text-anchor", "middle")
        .attr("startOffset", "50%")
        .attr("stroke", function (d) { return d.subTitleColor; })
        .attr("stroke-width", 0.5)
        .attr("font-size", width * 0.015 + "px")
        .text(function (d, i) { return "" + d.subTitle; });
    svg.selectAll('path.inner-rect')
        .data([chart.getInnerRectPoints()])
        .enter().append("path")
        .attr("d", function (d) { return "M" + d[0] + " L" + d[1] + " L" + d[2] + " L" + d[3] + " L" + d[0]; })
        .attr("stroke-width", function (d) { return 1; })
        .attr("fill", "rgba(255,255,255,0.35)")
        .attr("stroke", "rgba(0,0,0,1)")
        .attr("class", "inner-rect");
    svg.append("svg:defs")
        .selectAll("marker.diagonal")
        .data(chart.getDiagonalData())
        .enter().append("svg:marker")
        .attr("id", function (d) { return "arrowhead" + d.id; })
        .attr("refX", 10)
        .attr("refY", 0)
        .attr("markerWidth", 7)
        .attr("markerHeight", 7)
        .attr("orient", "auto")
        .attr("xoverflow", "visible")
        .attr("viewBox", '-0 -5 10 10')
        .append('svg:path')
        .attr('d', 'M 0,-5 L 10 ,0 L 0,5')
        .attr('stroke', function (d) { return d.color; })
        .attr("fill", 'none');
    svg.append("svg:defs")
        .selectAll("marker.diagonal-dot")
        .data(chart.getDiagonalData())
        .enter().append("svg:marker")
        .attr("id", function (d) { return "arrowhead-dot" + d.id; })
        .attr("refX", 0)
        .attr("refY", 0)
        .attr("markerWidth", 3)
        .attr("markerHeight", 3)
        .attr("orient", "auto")
        .attr("xoverflow", "visible")
        .attr("viewBox", '-5 -5 10 10')
        .append('svg:circle')
        .attr("r", 5)
        .attr('stroke', function (d) { return d.color; })
        .attr("fill", function (d) { return d.color; });
    var link = d3.linkHorizontal()
        .x(function (d) {
        return d.x;
    })
        .y(function (d) {
        return d.y;
    });
    svg.selectAll('path.diagonal')
        .data(chart.getDiagonalData())
        .enter().append("path")
        .attr("d", function (d) { return link(d); })
        .style("fill", "none")
        .style("stroke-width", "1px")
        .attr("class", "diagonal")
        .attr("stroke", function (d) { return d.color; })
        .attr("id", function (d, i) { return "diagonal" + i; })
        .attr('marker-end', function (d) { return "url(#arrowhead" + d.id + ")"; })
        .attr('marker-start', function (d) { return "url(#arrowhead-dot" + d.id + ")"; });
    svg.selectAll('text.diagonal')
        .data(chart.getDiagonalData())
        .enter().append("text")
        .attr("dy", -2)
        .attr("dx", -20)
        .attr("class", "diagonal")
        .attr("stroke", function (d) { return d.color; })
        .attr("stroke-width", 0.5)
        .append("textPath")
        .attr("xlink:href", function (d, i) { return "#diagonal" + i; })
        .style("text-anchor", "middle")
        .attr("startOffset", "50%")
        .attr("font-size", width * 0.014 + "px")
        .text(function (d) { return d.percent + "%"; });
    svg.selectAll('text.inner-rect-bottom')
        .data(chart.getInnerRectTexts())
        .enter().append("text")
        .attr("y", function (d) { return d.y; })
        .attr("x", function (d) { return d.x; })
        .attr("font-size", width * 0.014 + "px")
        .text(function (d) { return "" + d.text; });
    function wrap(text, width) {
        text.each(function () {
            var text = d3.select(this);
            var words = text.text().split(/\s+/).reverse();
            var word;
            var line = [];
            var lineNumber = 0;
            var lineHeight = 1.1; // ems
            var y = text.attr("y");
            var x = text.attr("x");
            var dy = 0;
            var tspan = text.text(null).append("tspan").attr("x", x).attr("y", y).attr("dy", dy + "em");
            while (word = words.pop()) {
                line.push(word);
                tspan.text(line.join(" "));
                if (tspan.node().getComputedTextLength() > width) {
                    line.pop();
                    tspan.text(line.join(" "));
                    line = [word];
                    tspan = text.append("tspan").attr("x", x).attr("y", y).attr("dy", ++lineNumber * lineHeight + dy + "em").text(word);
                }
            }
        });
    }
    svg.selectAll('text.right-phrase')
        .data([chart.getRightPhrase()])
        .enter().append("text")
        .attr("y", function (d) { return d.y; })
        .attr("x", function (d) { return d.x; })
        .attr("font-size", width * 0.014 + "px")
        .text(function (d) { return "" + d.text; })
        .call(wrap, 50);
};
